/* headings
 * =============================== */
.home-class-heading,
.home-testimonials-heading,
.home-blog-heading,
.faq-heading {
  text-align: center;
  font-size: 1.75rem;
  font-weight: bold;

  span {
    display: block;
    font-size: 1.5rem;
    font-weight: normal;
    margin-top: 0.5rem;
  }

  @include mq(tablet-sm) {
    font-size: 2.25rem;

    span {
      font-size: 1.75rem;
    }
  }
}

/* faq
 * =============================== */
.faq-section {
  padding: 0 1rem 4rem;
}

.faq-accordion {
  max-width: 800px;
  margin: 2rem auto 0;

  .faq-item {
    border-bottom: 1px solid #ddd;
    padding: 1rem 0;

    .faq-question {
      width: 100%;
      background: none;
      border: none;
      display: flex;
      align-items: center;
      justify-content: space-between;
      cursor: pointer;
      padding: 0.5rem 0;

      .faq-text {
        font-size: 1.125rem;
        font-weight: 600;
        color: #1f1f1f;
        text-align: left;
      }

      .faq-icon {
        font-size: 1.5rem;
        color: #07699e;
        transition: transform 0.3s ease;
        flex-shrink: 0;
      }

      &.active .faq-icon {
        transform: rotate(45deg); // 變減號
      }
    }

    .faq-answer {
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.4s ease, padding-top 0.4s ease;
      padding-top: 0;
      color: #666;
      font-size: 1rem;
      line-height: 1.6;
    }

    &.open .faq-answer {
      max-height: 500px;
      padding-top: 0.75rem;
    }
  }
}


/* home-banner-section
 * =============================== */
.home-banner-bg {
  position: relative;
}

.home-banner-wave {
  position: absolute;
  right: 0;
  bottom: 0;
  height: 192px;
  width: 100%;
  background-image: url("images/home-banner-wave.webp");
  background-position: 44% bottom;
  z-index: 5;
}

/* home-manifesto-section
 * =============================== */
.home-manifesto-bg {
  background-color: #f5f5f5;
}

.home-manifesto-bg h3 {
  font-size: 1.5rem;

  @include mq(mobile) {
    font-size: 2rem;
  }
}

.home-manifesto-bg h2 {
  display: none;
}

.manifesto-text {
  position: static; // 讓 submenu 佔據空間
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-in-out;
}

.manifesto-text.open {
  max-height: 40rem;
}

.manifesto-btn {
  width: 200px;
  margin: 1rem auto;
  padding: 0.75rem 1rem;
  border-radius: 25px;
  text-decoration: none;
}


/* home-class-section
 * =============================== */
.home-class-bg {
  position: relative;
  overflow: hidden;
}

/* Top background */
.bg-top,
.bg-mid,
.bg-bottom {
  position: absolute;
  left: 0;
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  z-index: 0;
}

.bg-top {
  top: 0;
  height: 300px;
  background-image: url('images/home-class-bg-top.webp');
  background-color: #f5f5f5;
}

.bg-mid {
  top: 290px;
  height: 1580px;
  background-image: url('images/home-class-bg-mid.webp');

  @include mq(tablet-sm) {
    height: 920px;
  }
}

.bg-bottom {
  bottom: 0;
  height: 280px;
  background-image: url('images/home-class-bg-bottom.webp');
  background-position: bottom;
  z-index: 1;

  @include mq(tablet-sm) {
    height: 200px;
  }

}

.home-class {
  position: relative;
  z-index: 2;
  padding-top: 10rem;
  padding-bottom: 12rem;

  &>h2,
  &>p {
    color: #fff;
  }
}

.home-class-wrapper {
  margin-top: 3.5rem;

  .home-class-btns {
    display: flex;
    justify-content: space-around;
    gap: 1rem;

    a {
      width: 100%;
      margin-bottom: 0;
    }
  }

  @include mq(tablet-sm) {
    display: flex;
    justify-content: space-between;
    gap: 1rem;

    .home-class-btns {
      display: block;

      a {
        margin-bottom: 1rem;
      }

      @include mq(laptop) {
        display: flex;
        justify-content: space-around;
        gap: 1rem;
      }
    }
  }

}

.home-class-section-btn {
  width: fit-content;
  margin: 3rem auto;

  @include mq(tablet) {
    display: flex;
    justify-content: space-around;
    gap: 2rem;

    a {
      width: 200px;
      margin-bottom: 0;
    }

  }
}

/* home-testomonials-section
 * =============================== */
.home-testimonials {
  margin-bottom: 5rem;
}

.home-testimonials-btn,
.home-blog-btn {
  width: fit-content;
  margin: 2rem auto;

}

/* home-blog-section
 * =============================== */

.blog-swiper .default-card {
  @include mq(tablet-sm) {
    width: 100%;
  }

  img {
    @include mq(laptop) {
      height: 200px;
    }
  }
}

.home-blog {
  padding-top: 4rem;
  max-width: 100%;
  overflow: visible;

  @include mq(laptop) {
    display: flex;
    gap: 5rem;
  }
}

.swiper.blog-swiper {
  width: 100%;
  padding-top: 2rem;
  position: relative;

  @include mq(laptop) {
    padding-top: 0;
    padding-left: 0;
    width: 60%;
    height: fit-content;
  }
}

.swiper-slide {
  box-sizing: border-box;
}

.home-blog-left {
  text-align: center;

  @include mq(laptop) {
    text-align: left;

    h2 {
      margin-top: 0;
    }

    span {
      text-align: left;
    }

    a {
      margin-left: 0;
    }
  }
}

.home-blog-bg {
  background-image: url("images/home-blog-bg.webp");
  background-position: left bottom;
  background-repeat: no-repeat;
  padding-bottom: 10rem;

  @include mq(laptop) {
    background-position: -5% 100%;
    background-size: 35%;
  }
}

.blog-btn {
  display: flex;
  gap: 4rem;
  justify-content: center;

  @include mq(laptop) {
    justify-content: start;
  }
}

.blog-next,
.blog-prev {
  svg {
    fill: #075b9a;
    height: 36px;
    width: 36px;
    border: #075b9a solid 2.1px;
    border-radius: 50%;
    opacity: 0.5;
    cursor: pointer;
  }

  svg:hover {
    background-color: #075b9a;
    fill: white;
    opacity: 1;
    transition: all 0.3s ease-in-out;
  }
}